<template>
  <div class="div-box">
    <!--flex弹性盒子模型，justify-content：主抽 -->
    <div style="display: flex;justify-content: center;margin-top: 150px">
      <el-card style="width: 400px">
        <div slot="header" class="clearfix">
          <span>登录</span>
        </div>

        <div>
          <el-form :label-position="labelPosition" :model="formInput" :rules="rules" ref="formInput" label-width="80px">
            <el-form-item label="账号" prop="name">
              <el-input v-model="formInput.name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="pass">
              <el-input v-model="formInput.pass" type="password" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item style="justify-content: center">
              <el-button type="primary" class="login-btn" @click="Login">登录</el-button>
              <el-button class="register-btn">注册</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Login',
  data () {
    return {
      labelPosition: 'top',
      formInput: {
        name: '',
        pass: '',
        type: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        pass: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    Login: function () {
      this.$refs.formInput.validate((valid) => {
        if (valid) {
          this.$options.methods.LoginAjax.bind(this)()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    LoginAjax: function () {
      const query = this.$qs.parse(
        {
          username: this.formInput.name,
          pass: this.$md5(this.formInput.pass)
        }
      )
      this.$axios({
        url: this.$ajaxUrl + '/LoginAjax',
        method: 'post',
        params: query
      }).then(res => {
        if (res.data.message === '成功') {
          sessionStorage.setItem('user', JSON.stringify(res.data.data))
          // this.$global = user,好像不能这面写
          this.$router.push('/main')
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          })
        }
      })
    }
  }

}
</script>

<style lang="scss" scoped>
.div-box{
  padding: 15px;
}
  .login-btn,.register-btn{
    width: 48%;
  }
</style>
