<template>

  <el-container  class="container" direction="vertical" style="height: 100%">
    <el-header style="height: 60px;" class="m-head">
      <div class="app-side-logo" >
        <img src="@/assets/logo.png"
             :width="show ? '60' : '60'"
             height="60" />

      </div>
      <div style="float: left">
        <i class="el-icon-s-operation" v-on:click="show = !show"></i>
      </div>
      <div style="line-height:60px;text-align: right;padding-right:15px;">
        <el-dropdown>
  <span class="el-dropdown-link" style="font-size: 18px;color: #fff">
    下拉菜单<i class="el-icon-arrow-down el-icon--right"></i>
  </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>黄金糕</el-dropdown-item>
            <el-dropdown-item>狮子头</el-dropdown-item>
            <el-dropdown-item>螺蛳粉</el-dropdown-item>
            <el-dropdown-item disabled>双皮奶</el-dropdown-item>
            <el-dropdown-item divided @click.native="SignOut">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-container class="container">
      <transition name="fade">
        <el-aside v-if="show" style="width: 200px;border-right: 1px solid #EBEEF5">
          <Menu></Menu>
        </el-aside>
      </transition>
      <el-main style="width: 100%">
        <el-breadcrumb separator="/" style="margin-bottom: 15px;">
          <el-breadcrumb-item v-for="(item,index) in list" :key="index" :to="{path:item.path}">
            {{item.name}}
          </el-breadcrumb-item>
        </el-breadcrumb>

        <transition name="move" mode="out-in">
          <router-view></router-view>
        </transition>
      </el-main>

    </el-container>
  </el-container>

  <!--    <router-view></router-view>-->
</template>

<script>
import Menu from '@/components/admin/Menu'

export default {
  name: 'Main',
  data () {
    return {
      isCollapse: true,
      show: true,
      dialogVisible: false,
      list: []
    }
  },
  watch: {
    $route () {
      this.bread()
    }
  },
  methods: {
    SignOut: function () {
      this.$confirm('确认退出吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(() => {
        sessionStorage.clear()
        this.$router.push(this.$sinOutUrl)
        this.startUsingLoading = false
      })
    },
    bread () {
      let matched = this.$route.matched.filter(item => item.name)
      const first = matched[0]

      if (first && first.name !== '首页') { // 我在这里是判断的是name，渲染的是name，但是可以使用其他的字段
        matched = [{ path: '/home', name: '首页' }].concat(matched)
      }

      this.list = matched
    }
  },
  components: {
    Menu
  },
  beforeCreate: function () {
    if (sessionStorage.getItem('user') == null) {
      this.$router.push(this.$sinOutUrl)
    }
  },
  created () {
    this.bread()
  },
  mounted () {
    this.$router.push('/videofilelist')
  }

}
</script>

<style scoped>
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
  .el-menu-vertical-demo
  {
    width: 200px;
  }

  .app-side-logo
  {
    width: 200px;
    text-align: center;
    float: left;
  }

  .m-head{
    background-color: #49b1f5;
  }
  .el-icon-s-operation{
    font-size: 40px;
    color: #fff;
    margin-top: 10px;
  }
</style>
