<template>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="yfid">
      <el-input v-model="form.fList.yfid"></el-input>
    </el-form-item>
    <el-form-item label="name">
      <el-input v-model="form.video_info.name"></el-input>
    </el-form-item>
    <el-form-item label="img">
      <el-input v-model="form.video_info.img"></el-input>
    </el-form-item>
    <el-form-item label="pingfen">
      <el-input v-model="form.video_info.pingfen"></el-input>
    </el-form-item>
    <el-form-item label="ename">
      <el-input v-model="form.video_info.ename"></el-input>
    </el-form-item>
    <el-form-item label="remark">
      <el-input type="textarea" v-model="form.video_info.remark"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="onSubmit">提交</el-button>
      <el-button v-on:click="back">返回</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'AddVideoFile',
  data () {
    return {
      form: {
        fList: {
          yfid: ''
        },
        video_info:
        {
          name: '',
          img: '',
          pingfen: '',
          ename: '',
          remark: ''
        }
      }
    }
  },
  beforeCreate: function () {
    if (sessionStorage.getItem('user') == null) {
      this.$router.push(this.$sinOutUrl)
    }
  },
  methods: {
    onSubmit () {
      const str = JSON.stringify(this.form)
      const query = this.$qs.parse({
        json: str
      })
      this.$axios.defaults.headers.common.token = JSON.parse(sessionStorage.getItem('user')).token
      this.$axios({
        url: this.$ajaxUrl + '/Api/AddVideoFileAjax',
        method: 'post',
        params: query
      }).then(res => {
        if (res.data === 10) {
          this.$message({
            message: '恭喜你，这是一条成功消息',
            type: 'success'
          })
          this.$router.push('/videofilelist')
        }
        this.$message.error('操作错误')
      })
    },
    back () {
      this.$router.go(-1)// 返回上一层
    }
  }
}
</script>

