import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '@/views/Login.vue'
import main from '@/views/admin/Main'
import home from '@/views/Home'
import videofilelist from '@/views/admin/VideoFile/VideoFileList'
import addvideofile from '@/views/admin/VideoFile/AddVideoFile'
import editvideofile from '@/views/admin/VideoFile/EditVideoFile'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/main',
    name: '面板',
    component: main,
    children: [
      {
        path: '/videofilelist',
        name: '视频文件列表',
        component: videofilelist
      },
      {
        path: '/addvideofile',
        name: '视频添加',
        component: addvideofile
      },
      {
        path: '/editvideofile',
        name: '视频编辑',
        component: editvideofile
      }
    ]
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

export default router
