<template>
  <div>
    <div style="text-align: center">
      <el-input placeholder="请输入内容" v-model="serach" class="input-with-select" style="width: 350px;">
        <el-select v-model="select" slot="prepend" placeholder="请选择">
          <el-option label="餐厅名" value="1"></el-option>
          <el-option label="订单号" value="2"></el-option>
          <el-option label="用户电话" value="3"></el-option>
        </el-select>
        <el-button slot="append" icon="el-icon-search" type="primary">查询</el-button>
      </el-input>
      &nbsp;&nbsp;&nbsp;&nbsp;
      <router-link to="/addvideofile"> <el-button type="primary">添加</el-button></router-link>
    </div>

    <el-table
      :data="tableData"
      style="width: 1920px"
      max-height="100%">
      <el-table-column
        v-if="false"
        prop="id"
        label="id">
      </el-table-column>
      <el-table-column
        fixed
        prop="wy_id"
        label="wy_id"
        width="310">
      </el-table-column>
      <el-table-column
        prop="name"
        label="name"
        width="220">
      </el-table-column>
      <el-table-column
        prop="remark"
        label="remark"
        width="300">
      </el-table-column>
      <el-table-column
        prop="img"
        label="img"
        width="300">
      </el-table-column>
      <el-table-column
        prop="pingfen"
        label="pingfen"
        width="100">
      </el-table-column>
      <el-table-column
        prop="e_name"
        label="e_name"
        width="120">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="220">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="EditData(scope.$index, tableData)"
            type="text"
            size="small">
            编辑
          </el-button>
          <el-button
            @click.native.prevent="deleteRow(scope.$index, tableData)"
            type="text"
            size="small">
            移除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>

</template>

<script>
export default {
  name: 'VideoFileList',
  methods: {
    deleteRow (index, rows) {
      const query = this.$qs.parse({ id: rows[index].id })
      this.$axios.defaults.headers.common.token = JSON.parse(sessionStorage.getItem('user')).token
      this.$axios({
        url: this.$ajaxUrl + '/Api/DeleteVideoFileAjax',
        method: 'post',
        params: query
      }).then(res => {
        if (res.data === 10) {
          this.$message({
            message: '恭喜你成功删除一条信息',
            type: 'success'
          })
          rows.splice(index, 1)
          return
        }
        this.$message.error('操作错误')
      })
      // rows.splice(index, 1)
    },
    EditData (index, rows) {
      this.$router.push({ path: '/EditVideoFile', query: { id: rows[index].id } })
    },
    GetPageList: function () {
      const query = this.$qs.parse(
        {
          pageNum: 0,
          pageSize: 10
        }
      )
      this.$axios.defaults.headers.common.token = JSON.parse(sessionStorage.getItem('user')).token
      this.$axios({
        url: this.$ajaxUrl + '/Api/VideoListPageAjax',
        method: 'post',
        params: query
      }).then(res => {
        if (res.data != null) {
          this.ParsePageList(res.data)
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          })
        }
      })
    },
    // 解析数据
    ParsePageList: function (data) {
      for (var i = 0; i < data.content.length; i++) {
        var item = data.content[i]
        var val = {}
        val.id = item.id
        val.wy_id = item.fid
        val.name = item.name
        val.remark = item.remark.length > 50 ? item.remark.substring(0, 50) + '......' : item.remark
        val.img = item.img
        val.pingfen = item.pingfen
        val.e_name = item.ename
        this.tableData.push(val)
      }
    }

  },

  data () {
    return {
      tableData: [],
      serach: ''
    }
  },
  mounted () {
    this.GetPageList()
  },
  beforeCreate: function () {
    if (sessionStorage.getItem('user') == null) {
      this.$router.push(this.$sinOutUrl)
    }
  }
}
</script>

<style scoped>

</style>
