<template>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="id" v-if="false">
      <el-input v-model="form.id"></el-input>
    </el-form-item>
    <el-form-item label="name">
      <el-input v-model="form.name"></el-input>
    </el-form-item>
    <el-form-item label="img">
      <el-input v-model="form.img"></el-input>
    </el-form-item>
    <el-form-item label="pingfen">
      <el-input v-model="form.pingfen"></el-input>
    </el-form-item>
    <el-form-item label="ename">
      <el-input v-model="form.ename"></el-input>
    </el-form-item>
    <el-form-item label="remark">
      <el-input type="textarea" v-model="form.remark"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit">提交</el-button>
      <el-button v-on:click="back">返回</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'EditVideoFile',
  data () {
    return {
      form: {
        name: '',
        img: '',
        pingfen: '',
        fid: '',
        ename: '',
        remark: '',
        id: ''
      }
    }
  },
  mounted () {
    this.loadMsg()
  },
  beforeCreate: function () {
    if (sessionStorage.getItem('user') == null) {
      this.$router.push(this.$sinOutUrl)
    }
  },
  methods: {
    onSubmit () {
      const query = this.$qs.parse(this.form)
      this.$axios.defaults.headers.common.token = JSON.parse(sessionStorage.getItem('user')).token
      this.$axios({
        url: this.$ajaxUrl + '/Api/EidtVideoFileAjax',
        method: 'post',
        params: query
      }).then(res => {
        if (res.data === 10) {
          this.$message({
            message: '恭喜你，这是一条成功消息',
            type: 'success'
          })
          return
        }
        this.$message.error('操作错误')
      })
    },
    back () {
      this.$router.go(-1)// 返回上一层
    },
    // 加载数据
    loadMsg () {
      var id = this.$route.query.id
      const query = this.$qs.parse(
        {
          id: id
        }
      )
      this.$axios.defaults.headers.common.token = JSON.parse(sessionStorage.getItem('user')).token
      this.$axios({
        url: this.$ajaxUrl + '/Api/GetVideoFileAjax',
        method: 'post',
        params: query
      }).then(res => {
        if (res.data.message === '成功') {
          this.form = res.data.data
        } else {
          this.$message({
            showClose: true,
            message: res.data.message,
            type: 'error'
          })
        }
      })
    }
  }

}
</script>

